to;\n  -moz-hyphens: auto;\n  -webkit-hyphens: auto;\n  hyphens: auto;\n  \n  overflow-x: hidden;\n  overflow-y: hidden;\n\n\n  height:90vh;\n\n  div{\n    // background-color: yellow;\n    background-color: var(--main-bg-color)\n  }\n\n  .header{\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    margin-top: 15px;\n    // margin-bottom: 10px;\n    place-items: center;\n\n    .archive_icon{\n      right: 0%;\n      position: relative;\n      color: var(--neutral-color);\n\n      .tooltiptext {\n        width: 200%;\n        left: -100px;\n        right: 100%;\n      }\n\n      button{\n        background:none;\n        border:none;\n        color: var(--neutral-color);\n      }\n      \n      .tooltiptext::after {\n        right: 75%;\n        margin-right: 0px;\n      }\n    }\n    .archive_icon:hover {\n      button{\n        color: var(--main-txt-color);\n      } \n      .tooltiptext {\n        visibility: visible;\n\n      }\n    }\n\n    .icon-button{\n      transition: filter 300ms;\n    }\n    .icon-button:hover{\n      /*filter: brightness(0.8)*/\n    }\n    .title-container{\n      text-align: left;\n      flex-grow: 8;\n      .th-title{\n        margin: 0;\n        font-weight: 900;\n        font-family: system-ui, -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, Ubuntu, \"Helvetica Neue\", sans-serif;\n        font-size: 19px;\n      }\n    }\n    \n  }\n\n  .robo{\n    display: flex;\n    flex-direction: row;\n    border: 1px solid var(--main-border-color);\n    border-radius: 3px;\n    padding: 8px 5px 8px 5px;\n    \n  }\n\n  .getRTs{\n    float: right;\n  }\n\n  .console{\n    \n  }\n\n  #filters{\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    color: var(--main-txt-color);\n    /*order: 1px solid var(--main-border-color);*/\n    border-radius: 3px;\n    padding: 0px 5px 0px 5px;\n  }\n\n  /* Dropdown Menu */\n  @import \"./icons.scss\";\n  \n\n  .searchWidget{\n    display: flex;\n    flex-direction: column;\n    white-space: pre-wrap;\n    height:auto; \n    overflow: hidden;\n    flex-grow: 1;\n    padding-top: 5px;\n  }\n  .searchTweets{\n    overflow-y: scroll;\n    overflow-x: hidden;\n  }\n\n  .th-text{\n    width: 100%;\n    white-space: pre-wrap;\n  }\n\n    \n  /* Dropdown Menu */\n  @import \"./dropdown.scss\";\n\n\n\n\n\n  /* tweets */\n  @import \"./tweet.scss\";\n\n\n}\n\n    \n/* width */\n.searchTweets::-webkit-scrollbar {\n  width: 10px;\n}\n\n/* Track */\n.searchTweets::-webkit-scrollbar-track {\n  background: var(--main-bg-color);\n}\n\n/* Handle */\n.searchTweets::-webkit-scrollbar-thumb {\n  background: var(--neutral-color);\n}\n\n/* Handle on hover */\n.searchTweets::-webkit-scrollbar-thumb:hover {\n  background: var(--main-txt-color);\n}\n\n\n.dummyContainer {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  pointer-events: none;\n  display: flex;\n}\n\n.dummyLeft {\n  flex-basis: 600px;\n  flex-grow: 1;\n}\n\n.dummyRight {\n  flex-grow: 1;\n  flex-basis: 0;\n  display: flex;\n  min-width: 0;\n}","\n  .sync{\n    height: 10px; \n    width: 10px;\n    border-radius: 50%;\n    margin-right: 5px;\n    position: relative;\n    \n    .tooltiptext{\n      width: 200px;\n      left: 100%;\n    }\n\n    &.synced {\n      background-color: rgb(0,200,0);\n    }\n  \n    &.unsynced{\n      background-color: rgb(255, 94, 0); \n    }\n  }\n\n\n  .robo-sync{  \n    position: relative;\n\n    &.unsynced::before {\n      content: \"...\";\n    }\n    &.synced::before {\n      content: \"\";\n    }\n\n    .tooltiptext{\n      width: 200px;\n      left: 100%;\n    }\n    /* Show the tooltip text when you mouse over the tooltip container */\n    \n    &:hover .tooltiptext {\n      visibility: visible;\n    }\n  }\n\n  /*For archive loading button*/\n  .tooltiptext{\n    visibility: hidden;\n    background-color:var(--main-txt-color);\n    color: var(--main-bg-color);\n    text-align: center;\n    font-weight: 100;\n    padding: 5px 0;\n    border-radius: 6px;\n   \n    /* Position the tooltip text - see examples below! */\n    position: absolute;\n    z-index: 1;\n    top: 110%;\n    margin-left: -20px; /*  Use half of the width (120/2 = 60), to center the tooltip */\n    a:visited {\n      color:var(--main-bg-color);\n    }\n  }\n\n  .sync .tooltiptext::after {\n    content: \" \";\n    position: absolute;\n    bottom: 100%;  /* At the top of the tooltip */\n    left: 10px;\n    margin-left: 0px;\n    border-width: 5px;\n    border-style: solid;\n    border-color: transparent transparent var(--main-txt-color);\n  }\n  /* Show the tooltip text when you mouse over the tooltip container */\n  .sync:hover .tooltiptext {\n    visibility: visible;\n  }\n\n  .filter-icon{\n    height:calc(17px);\n    width:calc(17px);\n    padding: 6px;\n  }\n  \n  .filter-icon:hover{\n    border-radius: 50%;\n    background-color: var(--neutral-color);\n  }\n  \n  .filter-checkbox{\n    display:none;\n  }\n\n  input[type=\"checkbox\"]:checked + label{\n    display:inline-block;\n    fill:var(--main-txt-color);\n    stroke: var(--main-txt-color);\n    stroke-width: 0.5px;\n    stroke-linejoin: round;\n  }\n  input[type=\"checkbox\"] + label{\n    display:inline-block;\n    fill:var(--neutral-color);\n  }\n\n  \n  .dropdown-icon{\n    border-radius: 50%;\n    fill:var(--neutral-color);\n  }\n  \n  .dropdown-icon:hover{\n    fill:var(--main-txt-color);\n  }\n  \n  ","\n\n  .dropdown {\n    --border-radius: 3px;\n    --border: 1px solid #474a4d;\n    --speed: 200ms; \n    position: absolute;\n    z-index:2;\n    top: 100px;\n    width: 115px;\n    transform: translateX(-75%);\n    background-color: var(--main-bg-color);\n    border: var(--border);\n    border-radius: var(--border-radius);\n    padding: 0rem;\n    overflow: hidden;\n\n      a {\n        color: var(--main-txt-color);\n      }\n    \n      .menu {\n        width: 100%;\n      }\n    \n      .menu-item {\n        height: 15px;\n        display: flex;\n        align-items: center;\n        border-radius: var(--border-radius);\n        transition: background var(--speed);\n        padding: 0.5rem;\n        text-decoration: none;\n      }\n    \n      .menu-item .icon-button {\n        margin-right: 0.5rem;\n      }\n    \n    \n      .menu-item .icon-button:hover {\n        filter: none;\n      }\n    \n      .menu-item:hover {\n        background-color: #525357;\n      }\n    \n      .icon-right {\n        margin-left: auto;\n      }\n\n  }    ",".th-tweet {\n  padding: 8px 5px 8px 5px;\n  margin-bottom: 5px;\n  flex-direction: row;\n  display: flex;\n  border: 1px solid var(--main-border-color);\n  border-radius: 3px;\n  color: var(--main-txt-color);\n  background: var(--main-bg-color);\n  -webkit-user-select: none;\n}\n\n.th-gutter {\n  flex-grow: 0;\n}\n\n.th-gutter img {\n  width: 35px;\n  height: 35px;\n  border-radius: 50%;\n}\n\n.th-body {\n  display: flex;\n  flex-grow: 1;\n  flex-direction: column;\n  padding-left: 5px;\n  min-width: 0;\n}\n\n.th-plus-container {\n  flex-grow: 0;\n}\n\n.th-header {\n  display: flex;\n}\n\n.th-header div {\n  padding-right: 4px;\n  padding-bottom: 3px;\n}\n\n.th-header-name {\n  font-weight: 700;\n  font-size: 14px;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  flex: 0 1 auto;\n}\n\n.th-header-username {\n  color: var(--neutral-color);\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  flex: 0 1 auto;\n}\n\n.th-header-time {\n  color: var(--neutral-color);\n  background-color: rgba(0, 0, 0, 0);\n  flex: 0 0 auto;\n  z-index: 1;\n}\n.th-header-time-link {\n  color: var(--neutral-color);\n  text-decoration: none;\n}\n.th-header-time-link:hover {\n  text-decoration: \"underline\";\n}\n\n.th-reply {\n  color: var(--neutral-color);\n}\n\n.th-text {\n  padding-top: 3px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: pre-wrap;\n}\n\n.th-media {\n  display: flex;\n  flex-direction: column;\n  max-height: 200px;\n  border: 1px solid var(--main-border-color);\n  border-radius: 8px;\n  margin-top: 8px;\n  overflow: hidden;\n}\n\n.th-media-image img {\n  max-width: 100%;\n  align-self: center;\n}\n\n.th-media-image {\n    min-width: 50%;\n    display: flex;\n}\n\n.th-media-top {\n    min-height: 50%;\n    flex-grow: 1;\n    display: flex;\n}\n\n.th-media-bottom {\n    min-height: 50%;\n    flex-grow: 1;\n    display: flex;\n}\n\n.th-tweet-container {\n  position: relative;\n  margin-top: 0px;\n}\n\n.th-quote {\n  display: flex;\n  flex-direction: column;\n  border: 1px solid var(--main-border-color);\n  border-radius: 8px;\n  margin-top: 8px;\n  padding: 5px 8px 5px 8px;\n}\n\n.th-quote-header {\n  display: flex;\n  align-items: bottom;\n}\n\n.th-quote-header-profile {\n  width: 15px;\n  height: 15px;\n  border-radius: 50%;\n}\n\n.th-quote-header-name {\n  padding-left: 5px;\n  font-weight: 700;\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  flex: 0 1 auto;\n}\n\n.th-quote-header-username {\n  padding-left: 5px;\n  padding-right: 5px;\n  color: var(--neutral-color);\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  flex: 0 1 auto;\n}\n\n.th-header-dot {\n  color: var(--neutral-color);\n}\n\n.th-quote-header-time {\n  padding-left: 5px;\n  color: var(--neutral-color);\n  background-color: rgba(0, 0, 0, 0);\n  flex: 1 0 auto;\n  z-index: 1;\n}\n\n.th-quote-header-time-link {\n  color: var(--neutral-color);\n  text-decoration: none;\n}\n.th-quote-header-time-link:hover {\n  text-decoration: \"underline\";\n}\n\n.th-quote-reply {\n  padding-top: 3px;\n  color: var(--neutral-color);\n}\n\n.th-quote-content {\n  display: flex;\n  padding-top: 3px;\n  padding-bottom: 3px;\n}\n\n.th-unavailable {\n  background-color: var(--main-background-color);\n  color: var(--neutral-color);\n}\n\n.th-quote-content-main {\n  display: flex;\n  flex-direction: column;\n  flex-grow: 1;\n  min-width: 0;\n}\n\n.th-quote-content-main-media {\n  display: flex;\n  flex-direction: column;\n  max-height: 150px;\n  margin-top: 10px;\n  overflow: hidden;\n}\n\n.th-quote-content-minimedia {\n  max-height: 90px;\n  max-width: 90px;\n  display: flex;\n  flex-direction: column;\n  padding-right: 5px;\n}\n\n.th-icons {\n  display: flex;\n  flex-grow: 1;\n  padding-top: 2px;\n  color: var(--neutral-color);\n}\n\n.th-icon-field {\n  display: flex;\n  flex-grow: 1;\n  div {\n    display: flex;\n    padding: 6px;\n    border-radius: 50%;\n    z-index: 1;\n    background-color: rgba(0, 0, 0, 0)\n  }\n}\n\n\n.th-icon-field div:hover {\n  cursor: pointer;\n}\n\n.th-icons svg {\n  width: 16px;\n  height: 16px;\n}\n\n.th-reply-container:hover {\n  background-color: rgba(38, 158, 228, .1);\n  svg {\n    color: rgb(38, 158, 228);\n  }\n}\n\n.th-share-container:hover {\n  background-color: rgba(38, 158, 228, .1);\n  svg {\n    color: rgb(38, 158, 228);\n  }\n}\n\n.th-rt-container:hover {\n  background-color: rgba(33, 182, 103, .1);\n  svg {\n    color: rgb(33, 182, 103);\n  }\n}\n\n.th-like-container:hover {\n  background-color: rgba(200, 52, 103, .1);\n  svg {\n    color: rgb(200, 52, 103);\n  }\n}\n\n.th-hover {\n  top: 0;\n  left: 0;\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  border-radius: 3px;\n  opacity: 0;\n  background: lightgray;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  -webkit-user-select: none;\n}\n\n.th-hover:hover {\n  transition: .1s ease;\n  cursor: pointer;\n  opacity: .7;\n}\n\n.th-hover-copy {\n    font-size: 16px;\n    font-weight: 500;\n    background-color: rgba(0, 0, 0, 0);\n}"]}]);
